<template>
  <view class="login-page">
    <!-- ËÉåÊôØÂõæÁâá -->
    <image class="bg-image" src="/static/login/beijing.jpg" mode="aspectFill"></image>
    
    <!-- Ë£ÖÈ•∞ÊÄßÂúÜÂΩ¢ -->
    <view class="decoration-circle circle-1"></view>
    <view class="decoration-circle circle-2"></view>
    <view class="decoration-circle circle-3"></view>

    <!-- ÂÜÖÂÆπÂ±Ç -->
    <view class="content-wrapper">
      <!-- Logo ÂíåÊ†áÈ¢ò -->
      <view class="logo-section">
        <view class="logo-container">
          <image class="logo" src="/static/login/logo.png" mode="aspectFit" />
          <view class="logo-glow"></view>
        </view>
        <text class="app-name">ÁîúËúúÊó∂ÂÖâ</text>
        <text class="app-slogan">ËÆ∞ÂΩïÊØè‰∏Ä‰∏™Áà±ÁöÑÁû¨Èó¥</text>
      </view>

      <!-- ÁôªÂΩïÂç°Áâá -->
      <view class="login-card">
        <!-- Áî®Êà∑‰ø°ÊÅØÈ¢ÑËßàÔºàÊéàÊùÉÂêéÊòæÁ§∫Ôºâ -->
        <view v-if="userInfo.nickName" class="user-preview">
          <image class="avatar" :src="userInfo.avatarUrl" mode="aspectFill" />
          <text class="nickname">{{ userInfo.nickName }}</text>
        </view>

        <!-- ÁôªÂΩïÊåâÈíÆ -->
        <button 
          v-if="!isLoggedIn" 
          class="login-btn" 
          @click="handleWxLogin"
          :loading="isLoading"
        >
          <view class="btn-content">
            <text class="btn-icon">üíï</text>
            <text class="btn-text">ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï</text>
          </view>
        </button>

        <!-- Ê∏∏ÂÆ¢ÁôªÂΩïÊåâÈíÆ -->
        <button 
          v-if="!isLoggedIn" 
          class="guest-btn" 
          @click="handleGuestLogin"
        >
          <view class="btn-content">
            <text class="btn-icon">‚ú®</text>
            <text class="btn-text">Ê∏∏ÂÆ¢ÁôªÂΩï</text>
          </view>
        </button>

        <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
        <view class="tips">
          <text class="tip-text">ÁôªÂΩïÂç≥Ë°®Á§∫ÂêåÊÑè</text>
          <text class="link-text">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
          <text class="tip-text">Âíå</text>
          <text class="link-text">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import http from '@/utils/http.js';
import config from '@/utils/config.js';

export default {
  data() {
    return {
      isLoggedIn: false,
      isLoading: false,
      userInfo: {
        nickName: '',
        avatarUrl: ''
      }
    };
  },
  onLoad() {
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁôªÂΩï
    this.checkLoginStatus();
  },
  methods: {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      try {
        const loginInfo = uni.getStorageSync('login_info');
        // Ê£ÄÊü•ÁôªÂΩï‰ø°ÊÅØÊòØÂê¶Â≠òÂú®Ôºå‰∏îÂåÖÂê´ÊúâÊïàÁöÑtoken
        const hasToken = loginInfo && (
          (loginInfo.token && loginInfo.token.trim()) ||
          (loginInfo.data?.token && loginInfo.data.token.trim()) ||
          (loginInfo.accessToken && loginInfo.accessToken.trim())
        );
        
        if (loginInfo && loginInfo.isLoggedIn && hasToken) {
          // Ê≥®ÊÑèÔºöËøôÈáåÂè™Ê£ÄÊü•Êú¨Âú∞ÊòØÂê¶ÊúâtokenÔºå‰∏çÈ™åËØÅtokenÊòØÂê¶ÊúâÊïà
          // Â¶ÇÊûútokenÂ∑≤ËøáÊúüÔºå‰ºöÂú®ÂêéÁª≠APIËØ∑Ê±ÇÊó∂Ë¢´ÂêéÁ´ØËøîÂõû401ÔºåÁÑ∂ÂêéÁî±handleUnauthorizedÂ§ÑÁêÜ
          console.log('Ê£ÄÊµãÂà∞Êú¨Âú∞ÁôªÂΩï‰ø°ÊÅØÔºåËá™Âä®Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ');
          console.log('‚ö†Ô∏è ÊèêÁ§∫ÔºöÂ¶ÇÊûútokenÂ∑≤ËøáÊúüÔºåÂ∞ÜÂú®ÂêéÁª≠ËØ∑Ê±ÇÊó∂Ëá™Âä®Â§ÑÁêÜ');
          this.isLoggedIn = true;
          this.userInfo = loginInfo.userInfo || {};
          // ‚úÖ Á´ãÂç≥Ë∑≥ËΩ¨Âà∞È¶ñÈ°µÔºå‰∏çÊòæÁ§∫ÁôªÂΩïÈ°µÈù¢
          // Â¶ÇÊûútokenÂ∑≤ËøáÊúüÔºå‰ºöÂú®È¶ñÈ°µÁöÑAPIËØ∑Ê±ÇÊó∂Ë¢´Ê£ÄÊµãÂà∞Âπ∂Â§ÑÁêÜ
          setTimeout(() => {
            uni.reLaunch({
              url: '/pages/index/index'
            });
          }, 300);
        } else if (loginInfo && loginInfo.isLoggedIn && !hasToken) {
          // ÁôªÂΩïÁä∂ÊÄÅ‰∏∫true‰ΩÜtokenÁº∫Â§±ÔºåÊ∏ÖÈô§Êó†ÊïàÁöÑÁôªÂΩï‰ø°ÊÅØ
          console.warn('‚ö†Ô∏è Ê£ÄÊµãÂà∞Êó†ÊïàÁöÑÁôªÂΩï‰ø°ÊÅØÔºàÁº∫Â∞ëtokenÔºâÔºåÊ≠£Âú®Ê∏ÖÈô§...');
          uni.removeStorageSync('login_info');
          this.isLoggedIn = false;
          this.userInfo = {};
          console.warn('‚úÖ Â∑≤Ê∏ÖÈô§Êó†ÊïàÁöÑÁôªÂΩï‰ø°ÊÅØÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        }
      } catch (e) {
        console.error('Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂ§±Ë¥•', e);
      }
    },

    /**
     * Ê∏∏ÂÆ¢ÁôªÂΩïÂ§ÑÁêÜ
     * Êó†ÈúÄÊéàÊùÉÔºåÁõ¥Êé•‰ΩøÁî®ÈªòËÆ§Áî®Êà∑‰ø°ÊÅØËøõÂÖ•Â∫îÁî®
     */
    handleGuestLogin() {
      // ÂàõÂª∫Ê∏∏ÂÆ¢Áî®Êà∑‰ø°ÊÅØ
      const guestUserInfo = {
        nickName: 'Ê∏∏ÂÆ¢Áî®Êà∑',
        avatarUrl: '/static/zhuye/smile.png',
        displayName: 'Ê∏∏ÂÆ¢Áî®Êà∑',
        displayAvatar: '/static/zhuye/smile.png',
        isGuest: true
      };

      // ‰øùÂ≠òÊ∏∏ÂÆ¢ÁôªÂΩï‰ø°ÊÅØ
      const loginInfo = {
        isLoggedIn: true,
        userInfo: guestUserInfo,
        isGuest: true, // Ê†áËÆ∞‰∏∫Ê∏∏ÂÆ¢ÁôªÂΩï
        loginTime: new Date().toISOString()
      };
      
      try {
        uni.setStorageSync('login_info', loginInfo);
        
        // ÊèêÁ§∫ÁôªÂΩïÊàêÂäü
        uni.showToast({
          title: 'Ê∏∏ÂÆ¢ÁôªÂΩïÊàêÂäü',
          icon: 'success',
          duration: 1500
        });

        // Âª∂ËøüË∑≥ËΩ¨Âà∞È¶ñÈ°µ
        setTimeout(() => {
          this.enterApp();
        }, 1500);
        
      } catch (error) {
        console.error('Ê∏∏ÂÆ¢ÁôªÂΩïÂ§±Ë¥•', error);
        uni.showToast({
          title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      }
    },

    /**
     * ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï‰∏ªÊµÅÁ®ã
     * ÊµÅÁ®ãËØ¥ÊòéÔºö
     * 1. Ë∞ÉÁî® uni.getUserProfile Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÔºàÊòµÁß∞„ÄÅÂ§¥ÂÉèÔºâ- ÂøÖÈ°ªÁî±Áî®Êà∑ÁÇπÂáªÁõ¥Êé•Ëß¶Âèë
     * 2. Ë∞ÉÁî® wx.login Ëé∑Âèñ‰∏¥Êó∂ÁôªÂΩïÂá≠ËØÅ code
     * 3. Â∞ùËØïË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïAPIÔºåÂ¶ÇÊûúÂ§±Ë¥•Âàô‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
     * 4. ÂâçÁ´Ø‰øùÂ≠òÁôªÂΩïÁä∂ÊÄÅÂíåÁî®Êà∑‰ø°ÊÅØ
     * 5. Áõ¥Êé•Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
     */
    async handleWxLogin() {
      this.isLoading = true;
      try {
        // 1. Ëé∑ÂèñÂæÆ‰ø°Áî®Êà∑Âü∫Á°ÄËµÑÊñô
        const userProfile = await uni.getUserProfile({
          desc: 'Áî®‰∫éÂÆåÂñÑÁî®Êà∑ËµÑÊñô'
        });
        
        // 2. Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcode
        const code = await this.getWxLoginCode();

        // 3. Â∞ùËØïË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïAPIÔºåÂ¶ÇÊûúÂ§±Ë¥•Âàô‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
        let loginResult;
        try {
          loginResult = await http.post(config.API.LOGIN.WECHAT, {
            code,
            nickName: userProfile.userInfo.nickName,
            avatarUrl: userProfile.userInfo.avatarUrl
          });
        } catch (apiError) {
          console.warn('ÂêéÁ´ØAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰ΩøÁî®Ê®°ÊãüÁôªÂΩï', apiError);
          // ‰ΩøÁî®Ê®°ÊãüÁôªÂΩïÊï∞ÊçÆ
          loginResult = {
            token: 'mock_token_' + Date.now(),
            openid: 'mock_openid_' + Date.now(),
            session_key: 'mock_session_' + Date.now(),
            success: true
          };
          
          // ÊèêÁ§∫Áî®Êà∑‰ΩøÁî®Ê®°ÊãüÁôªÂΩï
          uni.showToast({
            title: 'ÂêéÁ´ØÊúçÂä°Êú™Â∞±Áª™Ôºå‰ΩøÁî®Ê®°ÊãüÁôªÂΩï',
            icon: 'none',
            duration: 2000
          });
        }

        // 4. ‰øùÂ≠òÁôªÂΩï‰ø°ÊÅØÂà∞Êú¨Âú∞
        // Â§ÑÁêÜÂêéÁ´ØÂìçÂ∫îÊ†ºÂºèÔºöÊîØÊåÅÂ§öÁßçÊ†ºÂºè‰ª•ÂÖºÂÆπ‰∏çÂêåÊÉÖÂÜµ
        // Ê†áÂáÜÊ†ºÂºè: {success: true, message: "ÁôªÂΩïÊàêÂäü", data: {token: ..., openid: ..., session_key: ...}}
        // ÂÖºÂÆπÊ†ºÂºè1: {token: ..., openid: ..., session_key: ...}
        // ÂÖºÂÆπÊ†ºÂºè2: {success: true, token: ..., openid: ..., session_key: ...}
        // ÂÖºÂÆπÊ†ºÂºè3: {data: {success: true, data: {token: ...}}}
        // Áªü‰∏ÄÂ§ÑÁêÜÂìçÂ∫îÊï∞ÊçÆÔºåÂÖºÂÆπÂ§öÁßçËøîÂõûÁªìÊûÑ
        let responseData = loginResult;
        if (loginResult.data && typeof loginResult.data === 'object') {
          responseData = loginResult.data;
        }

        // ËßÑËåÉÂåñÂèØËÉΩÁöÑtokenÂ≠óÊÆµÔºàÊéíÈô§Á∫ØÊï∞Â≠óÁöÑÁä∂ÊÄÅÁ†ÅÔºâ
        const normalizeTokenCandidate = (candidate) => {
          if (typeof candidate !== 'string') {
            return '';
          }
          const trimmed = candidate.trim();
          if (!trimmed) {
            return '';
          }
          if (/^\d+$/.test(trimmed) && trimmed.length <= 6) {
            // ÂÉè 200 / 401 ËøôÊ†∑ÁöÑÁä∂ÊÄÅÁ†Å‰∏çËßÜ‰∏∫token
            return '';
          }
          return trimmed;
        };

        const tokenCandidates = [];
        const pushTokenCandidate = (candidate) => {
          const normalized = normalizeTokenCandidate(candidate);
          if (normalized) {
            tokenCandidates.push(normalized);
          }
        };

        if (responseData && typeof responseData === 'object') {
          pushTokenCandidate(responseData.token);
          pushTokenCandidate(responseData.data?.token);
        }
        pushTokenCandidate(loginResult.token);
        pushTokenCandidate(loginResult.data?.token);

        // ÂÖºÂÆπÂêéÁ´ØÁõ¥Êé•ÊäätokenÊîæÂú® data Â≠óÁ¨¶‰∏≤Êàñ code Â≠óÊÆµÈáåÁöÑÊÉÖÂÜµ
        if (typeof loginResult.data === 'string') {
          pushTokenCandidate(loginResult.data);
        }
        if (responseData && typeof responseData === 'string') {
          pushTokenCandidate(responseData);
        }
        pushTokenCandidate(loginResult.code);
        if (responseData && typeof responseData === 'object') {
          pushTokenCandidate(responseData.code);
        }

        const token = tokenCandidates.length > 0 ? tokenCandidates[0] : '';
        
        // Â∞ùËØï‰ªéÂ§ö‰∏™ÂèØËÉΩÁöÑË∑ØÂæÑËé∑Âèñopenid
        const openid = responseData.openid || 
                      responseData.user?.openid ||
                      loginResult.openid || 
                      loginResult.data?.openid || 
                      loginResult.data?.user?.openid ||
                      (responseData.data && responseData.data.openid) || 
                      '';
        
        // Â∞ùËØï‰ªéÂ§ö‰∏™ÂèØËÉΩÁöÑË∑ØÂæÑËé∑Âèñsession_keyÔºàÂèØÈÄâÂ≠óÊÆµÔºåÂêéÁ´ØÈÄöÂ∏∏‰∏çËøîÂõûÁªôÂâçÁ´ØÔºâ
        // Ê≥®ÊÑèÔºösession_key ‰∏ªË¶ÅÁî®‰∫éÂêéÁ´ØËß£ÂØÜÊïèÊÑüÊï∞ÊçÆÔºåÂâçÁ´ØÈÄöÂ∏∏‰∏çÈúÄË¶Å
        const sessionKey = responseData.session_key || 
                           responseData.sessionKey ||
                           responseData.user?.session_key ||
                           responseData.user?.sessionKey ||
                           loginResult.session_key || 
                           loginResult.sessionKey ||
                           loginResult.data?.session_key || 
                           loginResult.data?.sessionKey ||
                           loginResult.data?.user?.session_key ||
                           loginResult.data?.user?.sessionKey ||
                           (responseData.data && responseData.data.session_key) || 
                           (responseData.data && responseData.data.sessionKey) || 
                           undefined; // ‰ΩøÁî® undefined ËÄå‰∏çÊòØÁ©∫Â≠óÁ¨¶‰∏≤ÔºåË°®Á§∫Êú™Êèê‰æõ
        
        const isSuccess = loginResult.success !== false; // Â¶ÇÊûúÊ≤°ÊúâsuccessÂ≠óÊÆµÔºåÈªòËÆ§‰∏∫ÊàêÂäü
        
        const loginInfo = {
          isLoggedIn: true,
          token: token,
          openid: openid,
          sessionKey: sessionKey,
          userInfo: {
            nickName: userProfile.userInfo.nickName,
            avatarUrl: userProfile.userInfo.avatarUrl,
            displayName: userProfile.userInfo.nickName,
            displayAvatar: userProfile.userInfo.avatarUrl
          },
          loginTime: new Date().toISOString(),
          isMock: !isSuccess // Ê†áËÆ∞ÊòØÂê¶‰∏∫Ê®°ÊãüÁôªÂΩï
        };
        
        // Ë∞ÉËØï‰ø°ÊÅØÔºöÊ£ÄÊü•tokenÊòØÂê¶Ê≠£Á°ÆÊèêÂèñ
        if (process.env.NODE_ENV === 'development') {
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          console.log('üîç [ÁôªÂΩïÂìçÂ∫îÂàÜÊûê]');
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          console.log('üì¶ ÂéüÂßãÂìçÂ∫îÊï∞ÊçÆ:', loginResult);
          console.log('üì¶ ÂìçÂ∫îÊï∞ÊçÆÁ±ªÂûã:', typeof loginResult);
          console.log('üì¶ responseData:', responseData);
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          console.log('üîë TokenÊèêÂèñÁªìÊûú:');
          console.log('   - responseData.token:', responseData.token || 'Êú™ÊâæÂà∞');
          console.log('   - loginResult.token:', loginResult.token || 'Êú™ÊâæÂà∞');
          console.log('   - loginResult.data?.token:', loginResult.data?.token || 'Êú™ÊâæÂà∞');
          console.log('   - ÊúÄÁªàÊèêÂèñÁöÑToken:', token ? `‚úÖ Â∑≤ÊâæÂà∞ÔºåÈïøÂ∫¶: ${token.length}` : '‚ùå Êú™ÊâæÂà∞');
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          console.log('üë§ OpenIDÊèêÂèñÁªìÊûú:');
          console.log('   - responseData.openid:', responseData.openid || 'Êú™ÊâæÂà∞');
          console.log('   - responseData.user?.openid:', responseData.user?.openid || 'Êú™ÊâæÂà∞');
          console.log('   - loginResult.openid:', loginResult.openid || 'Êú™ÊâæÂà∞');
          console.log('   - loginResult.data?.openid:', loginResult.data?.openid || 'Êú™ÊâæÂà∞');
          console.log('   - loginResult.data?.user?.openid:', loginResult.data?.user?.openid || 'Êú™ÊâæÂà∞');
          console.log('   - ÊúÄÁªàÊèêÂèñÁöÑOpenID:', openid ? `‚úÖ Â∑≤ÊâæÂà∞: ${openid}` : '‚ùå Êú™ÊâæÂà∞');
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          console.log('üîê SessionKeyÊèêÂèñÁªìÊûú:');
          console.log('   - ÊúÄÁªàÊèêÂèñÁöÑSessionKey:', sessionKey ? `‚úÖ Â∑≤ÊâæÂà∞ÔºåÈïøÂ∫¶: ${sessionKey.length}` : '‚ÑπÔ∏è Êú™Êèê‰æõÔºàËøôÊòØÊ≠£Â∏∏ÁöÑÔºâ');
          console.log('   - üí° ËØ¥Êòé: session_key ‰∏ªË¶ÅÁî®‰∫éÂêéÁ´ØËß£ÂØÜÊïèÊÑüÊï∞ÊçÆÔºåÂâçÁ´ØÈÄöÂ∏∏‰∏çÈúÄË¶Å');
          console.log('‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ');
          if (!token) {
            console.error('‚ùå TokenÊèêÂèñÂ§±Ë¥•ÔºÅ');
            console.error('üìã ÂÆåÊï¥ÂìçÂ∫îÁªìÊûÑ:', JSON.stringify(loginResult, null, 2));
